@import '../../styles/modules';

.Header {
  background: $brand-primary;
  color: white;
  position: relative;
  transition: background 0.3s;
  display: flex;

  div {
    padding: 20px 40px 20px 20px;
    flex: 1 1 auto;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden; }

  &.theme--secondary {
    background: $brand-secondary; }

  &.theme--tertiary {
    background: $brand-tertiary; }

  .btn {
    color: white;

    &:hover {
      background: rgba(black, 0.1); } } }

